import React from 'react'

import { Switch, Route } from 'react-router-dom'

import { Home } from '../PaginasApp/Home'
import { DatosAPI } from '../PaginasApp/DatosAPI'
import { DatoTarjetaAPI } from '../PaginasApp/DatoTarjetaAPI'


export const Rutas = () => {
    return (
        <Switch>
            <Route path={'/DatosAPI/:id'} component={DatoTarjetaAPI} />
            <Route exact path={'/reactdeploy/'} component={Home} />
            <Route path={'/DatosAPI'} component={DatosAPI} />
        </Switch>
    )
}
