import React, { useState, useEffect } from 'react'
import { useParams } from 'react-router-dom'
import axios from 'axios'

export const DatoTarjetaAPI = () => {

    const { id } = useParams()
    const [datosTarjeta, setDatosTarjeta] = useState({})
    const [cargaDatos, setCargaDatos] = useState(false)

    useEffect(() => {
        const ObtenerDatosTarjeta = async (id) => {

            try {
                let datos = await axios.get(`https://jsonplaceholder.typicode.com/todos/${id}`)
                let datoObtenido = datos.data
                setCargaDatos(true)
                setDatosTarjeta(datoObtenido)
            } catch (e) {
                alert('No se puede conectar con el servidor')
            }

        }
        ObtenerDatosTarjeta(id)
        
    }, [])




    if (cargaDatos === false) {
        return (
            < div className="container-fluid mt-2" >
                <div className="card">
                    <div className="card-body">
                        <span>Cargando tarjeta...</span>
                    </div>
                </div>
            </div >
        )
    }


    return (
        <div className="container-fluid mt-2">
            <div className="card">
                <div className="card-body">
                    {datosTarjeta.id} - {datosTarjeta.title}
                </div>
            </div>
        </div>

    )
}
