import React, { useState, useEffect } from 'react'
import '../EstilosApp/DatosAPI.css'
import axios from 'axios'


import { TarjetaDatoAPI } from '../Componentes/TarjetaDatoAPI'

export const DatosAPI = () => {

    const [datosAPI, setDatosAPI] = useState([])
    const [cargaDatos, setCargaDatos] = useState(false)

    useEffect(() => {
        console.log("UseEffect DatosAPI Act")
        ObtenerdatosAPI()
    }, [])


    const ObtenerdatosAPI = async () => {

        try {
            let datosAPI = await axios.get('https://jsonplaceholder.typicode.com/todos/')
            let DatosObtenidos = datosAPI.data
            let estadoDatos = datosAPI.status
            console.log(estadoDatos)
            setCargaDatos(true)
            setDatosAPI(DatosObtenidos)
        } catch (e) {
            alert("No se pudo conectar con el servidor", e)
        }

    }


    if (cargaDatos === false) {
        return (
            <div className="contenedorCargaDatos">Cargando...</div>
        )
    }

    let mapeoDatosAPI = datosAPI.map((data, index) => {
        return (
            <TarjetaDatoAPI id={data.id} title={data.title} key={index} />
        )
    })

    return (
        <div className='contenedor-DatosAPI'>
            <div className="container-fluid">
                <span>DatosApi : {datosAPI.length}</span>
                <div className="row">
                    {mapeoDatosAPI}
                </div>
            </div>
        </div>
    )
}
