import React from 'react'

import {Link} from 'react-router-dom'

export const TarjetaDatoAPI = (props) => {
    return (
        <div className="col-md-3 mt-3">
            <div className="card">
                <div className="card-body">
                   <Link to={`/DatosAPI/${props.id}`}>{props.id} - {props.title}</Link> 
                </div>
            </div>
        </div>
    )
}
